header {

	.header-phone:before {
		content: ' ';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $head-foot;
		opacity: 0.7; }
	.header-phone {
		z-index: 10000;
		color: $head-caption;
		font-size: 22px;
		position: fixed;
		top: 50px;
		width: 100%;
		text-align: center;
		padding-top: 5px;
		padding-bottom: 5px;
		div {
			position: relative;
			display: inline-block;
			margin: 0 5px; } } }
.navbar:before {
	content: ' ';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: $head-foot;
	opacity: 0.5; }
.navbar {
	border: none;
	margin: 0;
	height: 50px;
	opacity: 1.5;
	li {
		position: relative;
		box-sizing: border-box;
		a {
			@include transition-ease(0.5s);
			color: $white;
			padding-left: 7px;
			padding-right: 7px;
			font-size: 18px;
			letter-spacing: 1px; } }

	li:hover a {
		color: $head-foot !important;
		background: $white !important; }

	.navbar-brand {
		color: $white;
		position: relative;
		font-size: 26px;
		@include flex;
		align-items: center;
		img {
			width: 30px;
			height: 30px;
			margin-right: 5px;
			display: inline-block; }
		span:nth-child(2) {
			font-weight: bold;
			color: $crimson;
			font-size: 32px;
			margin-right: 3px; } }

	.navbar-toggle {
		background: $head-foot;
		opacity: 0.8;
		span {
			background: $white; } }
	.navbar-nav {
		margin-top: 0;
		margin-bottom: 0; } }



.main-head-slider {
	height: 100%;

	.sl-slider-h {
		position: relative;
		text-align: center;
		top: 45%;
		left: 50%;
		font-weight: bold;
		font-size: 70px;
		color: $head-foot;
		display: inline-block;
		@include translate(-50%, -50%);
		span {}
		span:last-child {
			margin-top: 20px;
			font-size: 25px;
			display: block; }
 }		// left: 50%
	.sl-slide {
		.first-slide {
			text-align: center;
			@include flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			h2 {
				padding-top: 0;
				padding-bottom: 30px; }
			blockquote {
				border-left: none !important;
				p {
					font-size: 32px; }
				cite {
					font-size: 22px; } } }
		.colored {
			color: $head-caption; } }
	.container {
		padding: 0; }
	.cover:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $black !important;
		opacity: 0.3; }
	.phone {
		font-weight: bold;
		letter-spacing: 2px; }

	.sl-slide:nth-child(2) {
		h2 {
			margin-top: 40px; } }

	.carousel-inner {
 }		// height: 100vh

	.item {
		background: url('/img/slider-bg.png') no-repeat 0 50%;
		background-size: cover;
		// min-height: 550px
		height: 100%;
		text-align: center;
		// padding: 0 5% 20px
		color: $white;
		.content {
			position: absolute;
			top: 50%;
			left: 50%;
			@include translate(-50%, -50%); }
		h2 {
			text-shadow: 0 0 1px #000;
			padding: 20px;
			font-size: 34px;
			font-weight: 700;
			letter-spacing: 13px;
			text-transform: uppercase;
			width: 80%;
			text-align: center;
			line-height: 50px; } } }


