.pricing:before {
	content: '';
	position: absolute;
	top: 54%;
	left: 0;
	@include translate(0, -50%);
	width: 100%;
	height: 30%;
	background: $price;
	// background: url('/img/cources-bg.png') no-repeat 10% 50%
	background-size: cover;

	@include box-shadow( 0px 0px 20px 1px darken($grey, 30%));
 }	// opacity: 0.7
.pricing {
	background: darken($white,3%);
	// background: #06377e
	position: relative;
	padding-bottom: 30px;
	div, h2 {
		position: relative; }
	.prices-h {}

	// .prices-h:before,
	// .prices-h:after
	// 	width: 36%
	.pricing-content {
		@include flex;
		justify-content: space-around;
		flex-wrap: wrap;
		.pricing-slot:hover {
			@include transform(scale(1.05)); }
		.pricing-slot:hover button {
			@include rotate(360); }

		.pricing-slot {
			width: 20%;
			min-width: 220px;
			border-radius: 5px;
			background: $white;
			padding-bottom: 30px;
			margin: 0 5px 30px;
			@include transition-ease(0.5s);
			.pricing-slot-img {
				width: 100%;
				height: 150px;
				background-size: cover;
				overflow: hidden;
				img {
					border-radius: 5px 5px 0 0;
					width: 100%;
					height: 100%;
 } }					// min-height: 150px
			.pricing-slot-name {
				h4 {
					font-weight: bold;
					font-size: 20px;
					padding: 25px;
					text-align: center; } }
			button {
				position: absolute;
				bottom: 10px;
				right: 10px;
				background: none;
				background: lighten($price-kid, 00%);
				color: $white;
				border: none;
				border-radius: 5px;
				padding: 5px 15px;
				@include transition-ease(0.5s); }
			button:hover {
				@include box-shadow(0px 0px 10px 1px #000);
				background: $price-kid; }
			button:active,
			button:focus {
				background: darken($price-kid, 2%); } }

		.pricing-slot:nth-child(2) {
			button {
				background: $price-junior; }
			button:hover {
				background: $price-junior; }
			button:active,
			button:focus {
				background: darken($price-junior, 2%); } }
		.pricing-slot:nth-child(3) {
			button {
				background: $price-men; }
			button:hover {
				background: $price-men; }
			button:active,
			button:focus {
				background: darken($price-men, 2%); } }
		.pricing-slot:nth-child(4) {
			button {
				background: $price-online; }
			button:hover {
				background: $price-online; }
			button:active,
			button:focus {
				background: darken($price-online, 2%); } } } }

