.gallery {
	.gallery-foto {
		@include flex;
		justify-content: center;
		flex-wrap: wrap;
		margin-bottom: 50px;
		.fancybox-button:hover .fancybox-caption {
			opacity: 1;
			@include delay(0.1s); }
		.fancybox-button:hover .over {
			opacity: 0.5; }
		.fancybox-button {
			width: 28%;
			border-radius: 5px;
			// min-width: 200px
			// height: 100px
			position: relative;
			margin: 5px;
			border: 1px solid $white;
			overflow: hidden;
			img {
				width: 100%;
				height: 100%; }
			.over {
				width: 100%;
				height: 100%;
				border-radius: 5px;
				// background: rgba(0, 0, 0, 0.8)
				background: $black;
				position: absolute;
				top: 0;
				left: 0;
				opacity: 0;
				// @include box-shadow(inset 0px 0px 150px 10px #000)
				@include transition-ease(0.3s);
 }				// display: none
			.fancybox-caption {
				color: $white;
				font-size: 20px;
				letter-spacing: 2px;
				// text-transform: uppercase
				font-family: sans-serif;
				display: block;
				text-align: center;
				position: absolute;
				@include translate(0, -50%);
				top: 50%;
				left: 0;
				right: 0;
				opacity: 0;
				@include transition-ease(0.7s); } } } }



